"use client"

import { useRef } from "react"

export default function Home() {
	const sectionRef = useRef<HTMLDivElement>(null)
	const handleScroll = () => {
		const section = sectionRef.current
		if (!section) return

		const rect = section.getBoundingClientRect()
		const isVisible = rect.top < window.innerHeight && rect.bottom > 0

		if (isVisible && Math.abs(rect.top) > 100) {
			section.scrollIntoView({
				behavior: "smooth",
				block: "center",
			})
		}
	}

	return (
		<main className="flex flex-col gap-4">
			<section
				className="border-4 bg-amber-300 h-screen"
				onWheel={handleScroll} // detecta scroll
				ref={sectionRef}
				style={{
					height: "100vh",
					margin: "100px 0",
					border: "2px dashed tomato",
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
					fontSize: "2rem",
				}}
			>
				<p>1</p>
			</section>
			<section
				className="border-4 bg-red-300 h-screen"
				onWheel={handleScroll} // detecta scroll
				ref={sectionRef}
				style={{
					height: "100vh",
					margin: "100px 0",
					border: "2px dashed tomato",
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
					fontSize: "2rem",
				}}
			>
				<p>2</p>
			</section>
			<section
				className="border-4 bg-blue-300 h-screen"
				onWheel={handleScroll} // detecta scroll
				ref={sectionRef}
				style={{
					height: "100vh",
					margin: "100px 0",
					border: "2px dashed tomato",
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
					fontSize: "2rem",
				}}
			>
				<p>3</p>
			</section>
			<section
				className="border-4 bg-green-300 h-screen"
				onWheel={handleScroll} // detecta scroll
				ref={sectionRef}
				style={{
					height: "100vh",
					margin: "100px 0",
					border: "2px dashed tomato",
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
					fontSize: "2rem",
				}}
			>
				<p>4</p>
			</section>
		</main>
	)
}
